@import '~assets/styles/h3Imports';

.nav-link {
  align-items: center;
  border-bottom: 1px solid var(--menu-item-separator);
  color: var(--menu-item-feature);
  cursor: pointer;
  display: flex;
  font-weight: 600;
  min-height: 64px;
  padding: 0 9px 0 6px;
  text-decoration: none;

  & > div:first-child > span {
    display: flex;
  }

  svg {
    [data-fontsize='AAA'] & {
      height: auto;
      width: rem(24);
    }
  }
}

.feature-name {
  flex-grow: 1;
  font-size: rem(16);
  margin: 0 14px;

  [data-fontsize='AAA'] & {
    line-height: 1;
    padding: 10px 0;
  }
}

.active .feature-name {
  color: var(--menu-item-feature-active);
  font-weight: 700;
}

.nav-link:focus-visible {
  outline: 2px solid var(--menu-item-outline);
}

@mixin focus-hover {
  color: var(--menu-item-feature-hover);
  font-weight: 700;
}

// splitted selectors because of a bug of safari non compatibility with focus-visible
.nav-link:focus .feature-name {
  @include focus-hover;
}

.nav-link:focus-visible .feature-name {
  @include focus-hover;
}

.nav-link:hover .feature-name {
  @include focus-hover;
}

@mixin focus-hover-active {
  color: var(--menu-item-feature-active);
  font-weight: 700;
}

// splitted selectors because of a bug of safari non compatibility with focus-visible
.nav-link:focus.active .feature-name {
  @include focus-hover-active;
}

.nav-link:focus-visible.active .feature-name {
  @include focus-hover-active;
}

.nav-link:hover.active .feature-name {
  @include focus-hover-active;
}

.nav-link[aria-current='page'] {
  --icon-background: var(--icon-background-hover);
}
