@import '~assets/styles/h3Imports';

.root {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.arrow-button {
  border-radius: 50%;

  &__left {
    @media (max-width: $max-phone-size) {
      margin-left: em(-12);
    }
  }

  &__right {
    transform: rotate(180deg);

    @media (max-width: $max-phone-size) {
      margin-right: em(-12);
    }
  }

  &:disabled {
    & svg path {
      stroke: var(--input-disabled, #a5aaaa);
    }
  }

  & svg {
    min-height: em(24);
    min-width: em(24);
  }
}

.header {
  text-transform: capitalize;
}
