@import 'assets/styles/h3Imports';

.root {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

  main {
    display: flex;
    flex-grow: 1;
    gap: em(24);
  }

  .content {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    max-width: 100%;
    position: relative;
  }

  &[data-view='private'] {
    main {
      @include container;

      padding-bottom: em(24);
      padding-top: em(24);
    }

    .content {
      @media screen and #{$xlarge-up} {
        max-width: calc(100% - 346px);
      }
    }

    &[data-fourohfour='true'] {
      @media screen and #{$xlarge-up} {
        .content {
          max-width: 100%;
        }

        [data-layout='primary-menu'] {
          display: none;
        }
      }
    }
  }
}
