@import '~assets/styles/h3Imports';

.primary-menu {
  background-color: var(--primary-menu-background);
  border-radius: 16px;
  overflow: auto;
  pointer-events: initial;
  width: var(--primary-menu-width, 322px);

  @media #{$tablet-down} {
    min-height: calc(100vh - 63px);
  }

  ul {
    list-style: none;
    margin: rem(8) 0 rem(15);
    padding: 0;

    li {
      margin: 0;
      padding: 0 16px;

      @media #{$large-up} {
        [data-fontsize='AA'] & {
          padding: 0 13px;
        }
      }

      // Commented out until Support link returns
      // &:nth-last-child(2),
      &:last-child {
        a {
          border: none;
        }
      }
    }
  }
}

@media #{$xlarge-up} {
  .mobile {
    display: none;
  }

  .support-desktop {
    display: none;
  }
}

@media #{$large-down} {
  .support-desktop {
    display: block;
  }

  .primary-menu {
    border-radius: 0;

    // min-height: 865px;
    width: var(--primary-menu-mobile-width-mobile, 304px);

    ul {
      margin: rem(8) 0 rem(17);

      // li {
      //   &:nth-last-child(2) {
      //     a {
      //       border-bottom: 1px solid var(--menu-item-separator);
      //     }
      //   }
      // }
    }
  }
}
