@import '~assets/styles/h3Imports';

.root {
  align-items: center;
  border: em(2) solid transparent;
  border-radius: em(24);
  cursor: default;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: em(45);
  padding: em(13) em(8);
  position: relative;
  text-transform: capitalize;

  @media (max-width: $max-phone-size) {
    max-width: em(50);
  }

  &__today {
    border-color: var(--gray-3, #606565);
  }

  &__available {
    cursor: pointer;

    & .month-day {
      color: var(--accent-interactive, #284b9b);
    }

    &.appointment {
      &::after {
        background: var(--accent-interactive, #284b9b);
      }
    }
  }

  &__disabled {
    & .month-day {
      color: var(--input-disabled, #a5aaaa);
    }

    &.appointment {
      &::after {
        background: var(--input-disabled, #a5aaaa);
      }
    }
  }

  &__selected {
    --item-selected-background: var(--accent-selected, #274b9b);

    background: var(--item-selected-background);

    .week-day,
    .month-day {
      color: var(--white, #ffffff);
    }

    &.appointment {
      &::after {
        background: var(--white, #ffffff);
      }
    }
  }

  &.appointment {
    &::after {
      border-radius: 50%;
      bottom: em(4);
      content: '';
      height: em(6);
      left: 50%;
      position: absolute;
      transform: translateX(-50%);
      width: em(6);
    }
  }
}

.week-day {
  color: var(--gray-3, #606565);
  font-size: em(14);
  font-weight: 600;
}

.month-day {
  font-size: em(16);
  font-weight: 700;
  margin-top: var(--spacing-xxxs);

  @media (max-width: $max-phone-size) {
    margin-top: em(6);
  }
}

.cell-status {
  border: 0;
  clip: rect(0, 0, 0, 0);
  clip-path: inset(50%);
  height: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  white-space: nowrap;
  width: 0;
}
