/* stylelint-disable no-descending-specificity */
@import '~assets/styles/h3Imports';

$top: 4rem; // 64px;

.header {
  background-color: var(--header-background-color);
  display: flex;
  min-height: $top;
  position: relative;
  z-index: 10;

  [data-element='popover-profile'] {
    @media #{$xlarge-up} {
      margin-right: rem(6);
    }
  }

  [data-element='menu-button'] {
    min-height: 40px;
  }

  [data-element='support'] a,
  [data-element='logo-large'],
  [data-element='logo-small'],
  [data-element='open-search'],
  [data-element='menu-button'] {
    &:focus {
      border: 2px solid var(--text-button-outline);
      margin: -2px;
      outline: none;
    }

    &:focus-visible {
      border: none;
      margin: 0;
      outline: 2px solid var(--text-button-outline);
    }

    &:focus:not(:focus-visible) {
      border: none;
      outline: none;
    }
  }

  [data-element='logo-large'],
  [data-element='logo-small'],
  [data-element='signup'] a,
  [data-element='signin'] a,
  [data-element='menu-button'] {
    &:focus {
      margin: 0;
    }
  }
  @media #{$tablet-down} {
    [data-element='open-search'] {
      &:focus {
        margin: 0;
      }
    }
  }
}

.header-mobile {
  align-items: center;
  display: flex;

  & > a > img {
    min-width: rem(36);

    [data-fontsize='AA'] &,
    [data-fontsize='AAA'] & {
      margin-right: 10px;
    }
  }

  a,
  button {
    position: relative;
    z-index: 10;
  }

  button {
    padding: 0;
  }

  a:focus,
  a:focus-visible {
    border-radius: 8px;
    outline: rem(2) solid var(--text-button-outline);
  }
}

.logo-link {
  border-radius: 8px;
  display: block;

  &:focus:not(:focus-visible) {
    outline: none;
  }
}

.header-desktop {
  display: none;

  a:focus,
  a:focus-visible {
    border-radius: 8px;
    outline: rem(2) solid var(--text-button-outline);
  }
}

.menu {
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 3;
}

.menu-bg {
  background: var(--modal-overlay-background);
  bottom: 0;
  height: calc(100vh + 20px);
  left: 0;
  min-height: stretch;
  position: fixed;
  top: 0;
  width: 100%;
}

.menu-wrap {
  display: inline-block;
  height: calc(100vh - 2.8rem);
  left: 0;
  overflow: auto;
  position: absolute;
  top: 4rem;
  @media (max-width: 31.25em) {
    // pointer-events: none;
    right: 0;
  }
}

.menu[data-type='true'],
:global(.menu-active) .menu {
  display: block !important;
}

.menu[data-type='false'] {
  display: none;
}

.header-wraper {
  align-items: center;
  display: inline-flex;
  margin: auto;
  width: 100%;

  .left,
  .right {
    align-items: center;
    display: inline-flex;
  }

  .right {
    color: var(--white);
    margin-left: auto;
    z-index: 10;
  }

  .right div[data-type='hide-mobile'] {
    display: none;
  }

  .right > div {
    align-items: center;
    display: flex;

    > button span {
      color: var(--white);

      --icon-stroke: var(--white);
    }

    > span > svg {
      --icon-stroke: var(--white);
    }

    @media #{$small-up} {
      padding-left: 32px; //rem(32);
    }
  }
}

@media #{$small-only} {
  .header-wraper {
    .right > div > div > button {
      padding: rem(2);
    }
  }
}

@media #{$tablet-up} {
  .header-wraper {
    .left img {
      padding: rem(5);
    }
  }

  @media #{$tablet-only} {
    .header-wraper {
      .right {
        .search {
          max-width: rem(377);
        }
      }
    }
  }
}

@media #{$xlarge-up} {
  .header-wraper {
    .right div[data-type='hide-mobile'] {
      display: flex;
    }
  }

  .header-desktop {
    display: block;
  }

  .header-mobile {
    display: none;
  }
}
