@import '~assets/styles/h3Imports';

.finish-card {
  align-items: center;
  background-color: var(--background-2);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  display: flex;
  flex-direction: row;
  height: 100%;
  padding: rem(20);
  width: 100%;
}

.card-container {
  align-items: center;
  display: flex;
}

.title {
  @include h5;

  display: flex;
  margin: 0;
}

.finish-buttons {
  height: rem(40);
  margin-left: auto;
  width: rem(195);
}

.finish-card-button {
  align-content: center;
  height: 100%;
  width: 100%;
}

@media #{$tablet-down} {
  .finish-buttons {
    max-width: rem(120);
    min-width: rem(120);
  }
}

@media #{$small-down} {
  .finish-card {
    flex-direction: column;
    height: auto;
    padding: rem(16);
  }

  .finish-buttons {
    max-width: 100%;
    min-width: 100%;
  }
}
