@import '~assets/styles/h3Imports';

.root {
  align-items: center;
  border-color: var(--chip-border-color, var(--accent-interactive));
  border-radius: em(24);
  border-style: var(--chip-border-style, solid);
  border-width: var(--chip-border-width, em(1));
  display: flex;
  justify-content: center;
  padding: em(4) em(8) em(4) em(12);
  width: fit-content;

  [data-chip-element='content'] {
    font-size: em(14);
    font-weight: var(--chip-content-font-weight, 600);
    line-height: em(20);
    margin-right: em(8);

    button {
      --button-padding: 2px 5px;
      --button-font-size: var(--chip-button-font-size, inherit);
    }
  }

  [data-chip-element='remove'] {
    --button-text-padding: 0;
    --button-tiny-font-size: var(--chip-button-font-size, #{em(12)});

    transition: background-color 0.3s;

    &:hover {
      background-color: var(--text-button-hover-background);
      transition: background-color 0.3s;
    }

    &:focus {
      background-color: var(--text-button-hover-background);
      border-radius: em(8);
      outline: em(2) solid var(--accent-interactive);
      transition: background-color 0.3s;
    }

    &:active {
      background-color: var(--text-button-pressed-background);
      transition: background-color 0.3s;
    }
  }
}
